<template>
  <v-app>
    <v-card
    v-if="loginCard"
    >
      {{loginCardText}}
    </v-card>
    <div class="bodyBox">
      <div class="loginForm">
        <v-form
          ref="form"
          v-model="valid"
          lazy-validation
        >
          <v-row>
            <v-col>
              <v-text-field
                v-model="id"
                :rules="idRules"
                label="ID"
                required
                filled
                rounded
                dense
              ></v-text-field>
            </v-col>
          </v-row>
          <v-row>
            <v-col>
              <v-text-field
                v-model="pass"
                :rules="rules"
                class="input-group--focused"
                value=""
                :append-icon="show2 ? 'mdi-eye' : 'mdi-eye-off'"
                :type="show2 ? 'text' : 'password'"
                @click:append="show2 = !show2"
                label="PASS"
                required
                filled
                rounded
                dense
              ></v-text-field>
            </v-col>
          </v-row>
          <v-btn
            :disabled="!valid"
            color="success"
            class="mr-4"
            @click="validate"
            filled
            rounded
            dense
            type="submit"
          >
            Validate
          </v-btn>

          <v-btn
            color="error"
            class="mr-4"
            @click="reset"
            filled
            rounded
            dense
          >
            Reset Form
          </v-btn>
        </v-form>
      </div>
    </div>
  </v-app>
</template>

<script>
  export default {
    name: 'loginApp',
    data: () => {
      return {
        loginCard: false,
        loginCardText: 'aaa',
        id: '',
        idRules: [
          v => !!v || 'ID is required',
          v => /^[a-zA-Zㄱ-힣0-9]*$/.test(v) || 'Special characters cannot be entered',
        ],
        pass: '',
        rules: [
          v => !!v || 'Required.',
        ],
        show2: false,
      }
    },
    methods: {
      validate () {
        this.$axios.post('/api/userinfo/search', { id: this.id, pass: this.pass }).then(res => {
          this.loginCard = true
          console.log(res.data)
          this.loginCardText = res.data
        })
        // this.$router.push('/about')
      },
      reset () {
        // this.$refs.form.reset()
        this.$router.push('/about')
      },
    },
  }
</script>

<style>
  .bodyBox{
    width: 100%;
    height: 100%;
    text-align: center;
  }
  .loginForm{
    margin-top: 10%;
    display: inline-block;
    width: 900px;
    height: 1000px;
    /* background-color: red; */
  }
</style>
